html {
  box-sizing: border-box;
  font-size: 125%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  margin: 0;
  padding: 0;
}

#app {
  --primary-color: rgb(38, 123, 189);
  text-align: center;
}

#app .flow {
  display: flex;
  flex-direction: column;
  align-items: center;
}

header {
  background-color: var(--primary-color);
  color: white;
  position: -webkit-sticky;
  position: sticky;
  padding: 8px 16px;
}

header h1 {
  margin: 0;
}

#app main {
  flex-grow: 1;
  padding: 16px;
}

#app main > * {
  margin-bottom: 16px;
  width: 100%;
  max-width: 600px;
}

button {
  font-size: 150%;
  padding: 8px;
  background: var(--primary-color);
  color: white;
}

button:hover {
  cursor: pointer;
  box-shadow: inset 0 0 100px 100px rgba(0,0,0,.15);
}
.select-station {
  width: 100%;
  max-width: 600px;
  margin: 0;
}

.select-station:hover * {
  cursor: pointer;
}
.show-counts {
  width: 100%;
  max-width: 600px;
  background-color: lightgray;
  display: flex;
  padding: 8px;
  box-sizing: border-box !important;
}

.show-counts .count {
  flex: 1 0;
}

.hidden {
  visibility: hidden;
}
